a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

address {
  font-style: normal;
}

button {
  cursor: pointer;
  border: none;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h5, h6, p {
  margin: 0;
  padding: 0;
  font-weight: 400;
}

html {
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
}

body {
  position: relative;
}

header, section, footer {
  width: 100%;
}

.text-cursor-container {
  pointer-events: none;
  position: absolute;
  inset: 0;
}

.text-cursor-inner {
  width: 100%;
  height: 100%;
  position: relative;
}

.text-cursor-item {
  will-change: transform, opacity;
  position: absolute;
}

.text-type {
  white-space: pre-wrap;
  display: inline-block;
}

.text-type__cursor {
  opacity: 1;
  margin-left: .25rem;
  display: inline-block;
}

.text-type__cursor--hidden {
  display: none;
}

.container {
  max-width: 1246px;
  margin: 0 auto;
  padding: 0 15px;
  transition: all .5s;
}

@media screen and (width >= 362px) {
  .container {
    width: 362px;
  }
}

@media screen and (width >= 728px) {
  .container {
    width: 728px;
  }
}

@media screen and (width >= 1246px) {
  .container {
    width: 1246px;
  }
}

.header {
  background-color: #ea5d74;
  padding: 8px 0;
  position: fixed;
}

.header .container {
  position: relative;
}

.header__logo {
  letter-spacing: 1px;
  z-index: 10;
  width: 100px;
  font-family: Paytone One;
  font-size: 46px;
  font-weight: 400;
  line-height: 56px;
  transition: all .12s;
  display: block;
  position: relative;
}

.header__logo:hover {
  opacity: .8;
}

.header__logo-img {
  width: 35px;
  position: absolute;
  top: 0;
  right: 39px;
}
